import React from "react";
import './Camera.css'
import {Section} from "../../shared/ui/Section/Section";

interface CameraProps {
    videoRef: React.RefObject<HTMLVideoElement>; //    video,      
    canvasRef: React.RefObject<HTMLCanvasElement>; //    canvas,      
    isShow:Boolean, //  ,    
    isFaceDetected:Boolean,    // ,      
}

export const Camera: React.FC<CameraProps> = ({ videoRef, isShow,isFaceDetected,canvasRef }) => {
    return (
        <Section className={`video_box ${isShow?'':'hide'}`}>

            <video
                className="video_online"
                ref={videoRef}
                playsInline
                autoPlay
                muted
                controls={false}

            />
            <canvas
                ref={canvasRef}
                className={'canvas'}
            />
            {/*        */}
            <div
                className={`face_overlay ${isFaceDetected?'ok':'err'}`}

            />
        </Section>
    );
};
